@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.projects-container{
    padding:10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 10px;
    justify-content: center;
    text-align: center;
    margin-bottom: 0;
    padding-bottom: 0;
}

.project{
    display: flex;
    flex-direction: row;
    padding-top: 10px;
    margin: 20px;
    width: 347px;
    height: 103px;
    left: 38px;
    top: 149px;
    background: #F5F5FF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 15px;
    z-index: 1;
    margin-bottom: 20px;
    justify-content: center;
    text-align: center;
}

.showMoreProject{
background: #F5F5FF;
box-shadow: 6px 4px 10px rgba(0, 0, 0, 0.25);
border-radius: 15px;
width: 347px;
height: 52px;
border: none;
}


.projectImage{
   
    width: 76px;
    height: 76.81px;
    background: #4F51C0;
    border-radius: 63px;
    margin-left: 10px;
}
.projectImage svg{
    margin-top: 10px;
}


.projectTitle{
    width: 172px;
    height: 26px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    line-height: 26px;
    
    /* identical to box height */
    letter-spacing: -0.015em;
    
    color: #393939;
    
    
}

.company-container{
    justify-content: center;
    text-align: center;
    overflow: hidden;
}

.decoration{
    position: relative;
    margin-top: -350px;
    right: -700px;
    z-index: 0;
}

@media screen and (max-width: 1600px)
{
    .decoration{
        margin-top: -250px;
        right: -700px;
    }
}
@media screen and (max-width: 1450px)
{
    .decoration{
        margin-top: -200px;
        right: -500px;
    }
}
@media screen and (max-width: 1190px)
{
    .decoration{
        margin-top: -350px;
        right: -370px;
    }
}

@media screen and (max-width: 810px)
{
    .decoration{
       display: none;
    }
}

.projectDescriptionPage{
    display: flex;
    flex-direction: column;
    overflow: hidden;
    max-height: 900px;
}
.projectDescription{
    display: flex;
    flex-direction: row;
    padding: 20px;
    justify-content: center;
    text-align: center;
    
}
.projecttext{
    display: flex;
    flex-direction: column;
    width: 50%;
    padding-left:10% ;
    justify-content: center;
    text-align: left;
    z-index: 1;
    padding-right: 10%;
}
.descr{
font-family: Roboto;
font-style: normal;
font-weight: normal;
font-size: 30px;
line-height: 126.4%;

color: #2E2E2E;

}
.projectImg{
    margin-top: 100px;
    width: 400px;
    z-index: 0;
}
.descImg{
width: 400px;

}

.title{
width: 547px;
height: 108px;
width: 547px;
height: 108px;
font-family: Roboto;
font-style: normal;
font-weight: bold;
font-size: 50px;
line-height: 107.9%;
color: #2E2E2E;
margin-bottom: 30px;
}

.projectMaterial{
    padding: 10px;
}
.circleDeco{
    position: relative;
    margin-top: 5%;
    margin-left: 80%;
    transform: rotate(0deg);
}

